import React from "react";
import { getArtistById } from "@/lib/db/firestore-queries";
import { updateArtistAction } from "@/actions/artist-actions";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { notFound } from "next/navigation";

export default async function EditArtistPage({ params }: { params: { id: string } }) {
    const artist = await getArtistById(params.id);

    if (!artist) {
        notFound();
    }

    return (
        <div className="p-8 max-w-2xl mx-auto">
            <h1 className="text-3xl font-bold mb-6">Edit Artist</h1>
            <div className="bg-white dark:bg-neutral-900 p-6 rounded-lg shadow">
                <form action={updateArtistAction.bind(null, artist.id)} className="space-y-6">
                    <div className="space-y-2">
                        <Label htmlFor="name">Name</Label>
                        <Input id="name" name="name" defaultValue={artist.name} required />
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="bio">Bio</Label>
                        <Textarea id="bio" name="bio" defaultValue={artist.bio} required />
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="image">Image URL</Label>
                        <Input id="image" name="image" defaultValue={artist.image} required />
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                        <div className="space-y-2">
                            <Label htmlFor="youtube">YouTube URL</Label>
                            <Input id="youtube" name="youtube" defaultValue={artist.youtube} />
                        </div>
                        <div className="space-y-2">
                            <Label htmlFor="instagram">Instagram URL</Label>
                            <Input id="instagram" name="instagram" defaultValue={artist.instagram} />
                        </div>
                        <div className="space-y-2">
                            <Label htmlFor="twitter">Twitter URL</Label>
                            <Input id="twitter" name="twitter" defaultValue={artist.twitter} />
                        </div>
                        <div className="space-y-2">
                            <Label htmlFor="spotify">Spotify URL</Label>
                            <Input id="spotify" name="spotify" defaultValue={artist.spotify} />
                        </div>
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="color">Theme Color</Label>
                        <Input id="color" name="color" defaultValue={artist.color} />
                    </div>

                    <Button type="submit" className="w-full">Update Artist</Button>
                </form>
            </div>
        </div>
    );
}
